.user-image-container{
    width: 180px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
}
.profile{
    display: flex;
    margin-top: 10px;
    align-items: flex-start;
    background-color: whitesmoke;
    gap: 30px;
 
}
.showedContact{
    visibility: visible;
    transition:all 1s ease-in;
    -webkit-transition:all 1s ease-in;
    -moz-transition:all 1s ease-in;
    -ms-transition:all 1s ease-in;
    -o-transition:all 1s ease-in;
    list-style: none;
}
.hiddenContact{
    visibility: hidden;
    display: none;
  
}
.contacts{
    cursor: pointer;
    color: rgb(63, 63, 63);
    font-weight: 700;
}
.user{
    margin-left: 10px;
    margin-bottom: 2px;
    padding-top: 30px;
}
.profile__user-info{
    max-width: 40%;
    font-size: 15px;
}
.profile-content{
    background-color: rgb(252, 252, 252);
}
.user-image{
    object-fit: contain;
    width: 100%;
    height: 100%;
    border-radius: 10px;
}
.width_small{
    width:80px;
}
.user-name, .user-lastname{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    color: rgb(19, 19, 19);
    max-width: auto;
    margin: 0px;
    height: 40px;
}
.user-namimg{
    display: flex;
    gap: 10px;
    align-items: bottom;
    height: 40px;
}
.font_size_small *{
    font-size: 25px;
}